<template>
  <div class="financialTimes">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <van-cell v-for="item in list" :label="item.uploadTime" :title="item.jgjrrbTitle" :clickable="true" :to="'/mall/gtja/financialTimes/' + item.fileId + '/' + item.jgjrrbTitle"/>
      </van-list>

    </van-pull-refresh>

  </div>
</template>

<script>
  export default {
    name: "financialTimes",
    data() {
      return {
        title: '结构金融日报',
        isLoading: false,
        loading: false,
        finished: false,
        list: [],
        pageNum: 1,
        pageSize: 10,
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        this.getDataList(cfaeIp + '/mall/jgjrrb/getJgjrrbList.json?pageNum=1&pageSize=' + this.pageNum * this.pageSize, (data) => {
          this.list = data.result;
          this.$toast('刷新成功');
          this.isLoading = false;
        });
      },
      onLoad() {
        this.getDataList(cfaeIp + '/mall/jgjrrb/getJgjrrbList.json?pageNum=' + this.pageNum + '&pageSize=' + this.pageSize, (data) => {
          this.list = this.list.concat(data.result);
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          if (this.list.length >= data.totalCount) {
            this.finished = true;
          } else {
            this.pageNum++;
          }
        });
      },
      getDataList(url,fn){
        this.$http.post(backstageIp + userUrl + '/getFlagshipData',
          {
            url: url,
            type: 'get'
          }
        ).then(res => {
          if (res.data.success) {

            let data = JSON.parse(res.data.data).data;
            for (let i = 0; i < data.result.length; i++) {
              data.result[i].jgjrrbTitle = data.result[i].jgjrrbTitle.replace('.pdf', '');
              data.result[i].uploadTime = data.result[i].uploadTime.slice(0, 10);
            }
            console.log(data);
            fn && fn(data);
          } else {
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          }
        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        });
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>

  .van-pull-refresh {
    margin-top: 46px;
    background: rgba(247, 248, 250, 1);
  }
  >>>.van-pull-refresh__track {
    min-height: calc(100vh - 46px);
  }

  >>>.van-cell {
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    padding: 12px 15px;
  }
  .van-cell span {
    display: block;
    max-height: 45px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  .van-cell__label {
    font-size: 12px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(155, 155, 155, 1);
    line-height: 16.5px;
    margin-top: 5px;
  }

</style>



